.card {
  &__photos {
    margin-bottom: 16px;
  }

  &__photo-container {
    position: relative;

    width: 100%;
    max-height: 188px;
    overflow: hidden;

    &::after {
      content: "";
      display: block;
      padding-bottom: 100%;
    }

    @include onPhablet {
      height: 188px;
      max-height: 320px;
      padding-bottom: 0;
    }

    @include onDesktop {
      height: 320px;
      padding-bottom: 0;
    }
  }

  &__photo {
    position: absolute;
    width: 100%;
    height: 100%;

    object-fit: cover;
    object-position: center;

    transition: transform 0.3s;

    &:hover {
      transform: scale(1.1);
    }
  }
}
